<template>
  <div class="modal fade show" tabindex="-1" style="display: block;" aria-modal="true" role="dialog">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">{{ product.title }}</h5>
          <button type="button" class="btn-close" @click="$emit('close')"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
              <img :src="product.image_url" alt="Product Image" class="img-fluid">
            </div>
            <div class="col-md-6">
              <h6>{{ product.product_type }}</h6>
              <!-- Ratings and Store Link -->
              <div class="d-flex align-items-center mb-3">
                <div>
                  <i
                      v-for="i in 5"
                      :key="i"
                      :class="i <= product.avg_rating ? 'bi bi-star-fill text-warning' : 'bi bi-star text-warning'"
                  ></i>
                  <span v-if="product.avg_rating > 0">({{ product.avg_rating.toFixed(2) }})</span>
                </div>
              </div>
              <p>Price: {{ formatPrice(product.price) }}</p>

              <!-- Color Selection -->
              <h5 v-if="product.colors?.length > 0">Select Color</h5>
              <div class="d-flex flex-wrap">
                <label class="custom-radio-button color-radio mb-0" v-for="(color, index) in product.colors" :key="index">
                  <input
                      type="radio"
                      name="color"
                      :value="color"
                      v-model="selectedColor"
                      class="d-none"
                      @change="handleChangeColor"
                  />
                  <span class="color-square">{{ color }}</span>
                </label>
              </div>
              <span v-if="colorError" class="text-danger">Please select a color</span>

              <!-- Size Selection -->
              <h5 class="mt-3" v-if="product.sizes?.length > 0">Select Size</h5>
              <div class="d-flex flex-wrap">
                <label class="custom-radio-button size-radio mb-0" v-for="(size, index) in product.sizes" :key="index">
                  <input
                      type="radio"
                      name="size"
                      :value="size"
                      v-model="selectedSize"
                      class="d-none"
                      @change="handleChangeSize"
                  />
                  <span class="size-square">{{ size }}</span>
                </label>
              </div>
              <span v-if="sizeError" class="text-danger">Please select a size</span>

              <div>
                <button class="btn btn-success mt-3 cart-btn" @click="addToCart">Add to Cart</button>
                <button class="btn btn-outline-danger mt-3 ms-2" @click="addToWishlist">Add to Wishlist</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { defineProps } from 'vue';
import {request} from "@/helpers/request";
import {useStore} from "@/stores/mutations";
import router from "@/router";
import toastr from 'toastr';
import {useAuthStore} from "@/stores/auth";
const authStore = useAuthStore()

const props = defineProps({
  product: {
    type: Object,
    required: true,
  },
});

const selectedColor = ref(null);
const selectedSize = ref(null);
const colorError = ref(false);
const sizeError = ref(false);

function formatPrice(price) {
  return `৳${price.toFixed(2)}`;
}

function addToCart() {
  if (!selectedColor.value && props.product?.colors?.length > 0 && !selectedSize.value && props.product?.sizes?.length > 0) {
    colorError.value = true;
    sizeError.value = true;
    return;
  }

  if (!selectedColor.value && props.product?.colors?.length > 0) {
    colorError.value = true;
    return;
  }

  if (!selectedSize.value && props.product?.sizes?.length > 0) {
    sizeError.value = true;
    return;
  }

  request.post('cart/add', {
    id: props.product?.id,
    name: props.product?.title,
    quantity: 1,
    price: props.product?.price,
    image: props.product?.image_url,
    color: selectedColor.value,
    size: selectedSize.value
  })
      .then(response => {
        //console.log(response.data)
        useStore().setCartDetails(response.data)
        toastr.success('Product added to cart', 'Success');
        router.push('/cart/details')
      })
      .catch(error => {
        console.log(error)
      });
}

function addToWishlist() {
  let isAuthenticated = authStore.getAuthenticatedStatus();
  if (!isAuthenticated) {
    toastr.error('Please login to add product to wishlist', 'Error');
    return;
  }

  if (!selectedColor.value && props.product?.colors?.length > 0 && !selectedSize.value && props.product?.sizes?.length > 0) {
    colorError.value = true;
    sizeError.value = true;
    return;
  }

  if (!selectedColor.value && props.product?.colors?.length > 0) {
    colorError.value = true;
    return;
  }

  if (!selectedSize.value && props.product?.sizes?.length > 0) {
    sizeError.value = true;
    return;
  }

  request.post('wishlist/add', {
    product_id: props.product?.id,
    qty: 1,
    color: selectedColor.value,
    size: selectedSize.value
  })
      .then(response => {
        toastr.success('Product added to wishlist', 'Success');
        router.push('/wishlist/details')
      })
      .catch(error => {
        toastr.error(error?.response?.data?.error, 'Error');
      });
}

const handleChangeColor = () => {
  if (!selectedColor.value) {
    colorError.value = true;
  } else {
    colorError.value = false;
  }
}

const handleChangeSize = () => {
  if (!selectedSize.value) {
    sizeError.value = true;
  } else {
    sizeError.value = false;
  }
}
</script>

<style scoped>
.modal {
  background-color: rgba(0, 0, 0, 0.5);
}
</style>