<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link
          :to="{ name: 'UserDashboard' }"
          class="btn btn-light icon-circle"
        >
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center fw-bold">Order History</h3>
      </div>
    </div>

    <!-- Orders List -->
    <div v-if="orders?.data?.length > 0" class="orders-list">
      <div
        v-for="order in orders?.data"
        :key="order.id"
        class="order-card mb-4 p-3 border rounded"
      >
        <h5>Invoice #: {{ order.id }}</h5>
        <p><strong>Customer:</strong> {{ order.fullname }}</p>
        <p><strong>Grand Total:</strong> ৳{{ order.grand_total }}</p>
        <p>
          <strong>Status:</strong>
          <span :class="getStatusBadge(order.order_status)">{{
            getStatusText(order.order_status)
          }}</span>
        </p>
        <p><strong>Courier Status:</strong> {{ order.courier_status }}</p>
        <p>
          <strong>Created At:</strong>
          {{ new Date(order.created_at).toLocaleDateString() }}
        </p>
        <router-link
          :to="{ name: 'OrderDetails', params: { id: order.id } }"
          class="btn btn-outline-warning"
          >Details</router-link
        >
      </div>

      <div class="d-flex justify-content-center">
        <vue-awesome-paginate
          :total-items="ordersCount"
          :items-per-page="orders?.meta?.per_page"
          :max-pages-shown="3"
          v-model="currentPage"
          :onClick="onClickHandlerPagination"
        >
          <template #prev-button>
            <span>
              <i class="bi bi-arrow-left"></i>
            </span>
          </template>

          <template #next-button>
            <span>
              <i class="bi bi-arrow-right"></i>
            </span>
          </template>
        </vue-awesome-paginate>
      </div>
    </div>

    <!-- No Orders Message -->
    <div v-else class="text-center mt-4">
      <p>No orders found.</p>
    </div>

    <!-- Done Button -->
    <div class="text-center mt-4">
      <router-link
        :to="{ name: 'UserDashboard' }"
        class="btn btn-outline-primary"
        >Done</router-link
      >
    </div>
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import { useStore } from "@/stores/mutations";
import { onMounted, ref } from "vue";
import { VueAwesomePaginate } from "vue-awesome-paginate";
import { useRoute, useRouter } from "vue-router";

const router = useRouter();
const store = useStore();
const orders = ref([]);
const ordersCount = ref(0);
const route = useRoute();
const currentPage = ref(route.query.page ?? 1);

const fetchOrders = async (currentPage) => {
  try {
    const response = await request.get("dashboard/orders", {
      params: { currentPage: currentPage },
    });
    orders.value = response.data.data.orders;
    ordersCount.value = response.data.data.orders.total;
  } catch (error) {
    console.error("Error fetching orders:", error);
  }
};

const getStatusText = (status) => {
  switch (status) {
    case 0:
      return "Processing";
    case 1:
      return "Confirm";
    case 2:
      return "Shipped";
    case 3:
      return "Delivered";
    case 4:
      return "Cancelled";
    case 5:
      return "Success";
    default:
      return "Unknown";
  }
};

const getStatusBadge = (status) => {
  switch (status) {
    case 0:
      return "badge bg-warning"; // Processing
    case 1:
      return "badge bg-primary"; // Confirm
    case 2:
      return "badge bg-info"; // Shipped
    case 3:
      return "badge bg-success"; // Delivered
    case 4:
      return "badge bg-danger"; // Cancelled
    case 5:
      return "badge bg-success"; // Success
    default:
      return "badge bg-secondary"; // Unknown or other
  }
};

const onClickHandlerPagination = (page) => {
  currentPage.value = page;
  router.push({
    path: route.path,
    query: {
      ...route.query,
      page: page,
    },
  });

  fetchOrders(currentPage.value);
};

onMounted(() => {
  fetchOrders(currentPage.value);
});
</script>

<style scoped>
.container {
  max-width: 500px;
  margin: auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

.order-card {
  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

h1,
h5 {
  font-weight: bold;
}
</style>
