<template>
  <div class="p-4 pb-0">
      <router-link to="/">
          <span><i class="bi bi-arrow-left-circle fs-1"></i></span>
      </router-link>
  </div>

  <div class="container mt-2 d-flex justify-content-center align-items-center">
    <div class="p-4" style="width: 100%; max-width: 400px;">
      <h2 class="text-center fw-bold">Create Account</h2>
      <p class="text-center mb-4">Let’s Create Account Together</p>
      <form @submit.prevent="register">
        <div class="mb-3">
          <label for="name" class="form-label">Full Name</label>
          <input type="text" class="form-control" id="name" v-model="form.name" placeholder="Enter Your Full Name" autofocus autocomplete="off" />
          <span v-if="errors.name" class="text-danger">{{ errors.name[0] }}</span>
        </div>
        <div class="mb-3">
          <label for="email" class="form-label">Email</label>
          <input type="email" class="form-control" id="email" v-model="form.email" placeholder="Enter Your Email" autofocus autocomplete="off" />
          <span v-if="errors.email" class="text-danger">{{ errors.email[0] }}</span>
        </div>
        <div class="mb-3">
          <label for="password" class="form-label">Password</label>
          <input type="password" class="form-control" id="password" v-model="form.password" placeholder="********" autocomplete="off" />
          <span v-if="errors.password" class="text-danger">{{ errors.password[0] }}</span>
        </div>
        <div class="mb-3">
          <label for="password_confirmation" class="form-label">Password Confirmation</label>
          <input type="password" class="form-control" id="password_confirmation" v-model="form.password_confirmation" placeholder="********" autocomplete="off" />
        </div>
        <div class="d-flex justify-content-between align-items-center">
          <button type="submit" class="btn btn-primary w-100 auth-btn">Sign Up</button>
        </div>
      </form>
      <div class="mt-3 text-center">
        <p class="text-muted">Already have an account? <router-link to="/login" class="text-decoration-none text-dark">Sign In</router-link></p>
      </div>
    </div>
  </div>
</template>

<script setup>
import {reactive, ref} from 'vue';
import {request} from "@/helpers/request";
import {setToken} from "@/helpers/auth";
import {useRouter} from "vue-router";
import {useStore} from "@/stores/mutations";
import {useAuthStore} from "@/stores/auth";
import toastr from 'toastr';
const isLoggedIn = ref(false)
const router = useRouter()
const store = useStore()
const authStore = useAuthStore()

const form = reactive({
  name: '',
  email: '',
  password: '',
  password_confirmation: '',
})

const errors = ref({
  name: '',
  email: '',
  password: '',
  password_confirmation: '',
})

const register = async () => {
  await request.post('register', {
    name: form.name,
    email: form.email,
    password: form.password,
    password_confirmation: form.password_confirmation,
  })
      .then(response => {
        console.log(response.data)
        isLoggedIn.value = true
        setToken(response.data.token);
        store.setToken(response.data.token);
        store.setUser(response.data.user);
        authStore.login();
        authStore.setAuthenticatedStatus(true);
        toastr.success('Registered success', 'Success');
        router.push('/user/dashboard')
      })
      .catch(error => {
        if (error.response.data.errors) {
          errors.value = error.response.data.errors
        }
      });
}
</script>

