import { useAuthStore } from "@/stores/auth";
import ForgotPassword from "@/views/Auth/ForgotPassword.vue";
import LoginPage from "@/views/Auth/LoginPage.vue";
import RegisterPage from "@/views/Auth/RegisterPage.vue";
import ResetPassword from "@/views/Auth/ResetPassword.vue";
import CartDetails from "@/views/Cart/CartDetails.vue";
import CheckOut from "@/views/Cart/CheckOut.vue";
import OrderComplete from "@/views/Cart/OrderComplete.vue";
import WishlistDetails from "@/views/Cart/WishlistDetails.vue";
import ProductsBrand from "@/views/Product/ProductsBrand.vue";
import ProductsCategories from "@/views/Product/ProductsCategories.vue";
import ProductsPage from "@/views/Product/ProductsPage.vue";
import ShopByBrand from "@/views/Product/ShopByBrand.vue";
import ShopByCategory from "@/views/Product/ShopByCategory.vue";
import ShopByChildCategory from "@/views/Product/ShopByChildCategory.vue";
import ShopBySubCategory from "@/views/Product/ShopBySubCategory.vue";
import SingleProduct from "@/views/Product/SingleProduct.vue";
import ContactComplete from "@/views/User/ContactComplete.vue";
import ContactUs from "@/views/User/ContactUs.vue";
import EditProfile from "@/views/User/EditProfile.vue";
import NotificationDetails from "@/views/User/NotificationDetails.vue";
import OrderDetails from "@/views/User/OrderDetails.vue";
import OrderHistory from "@/views/User/OrderHistory.vue";
import PrivacyPolicy from "@/views/User/PrivacyPolicy.vue";
import TermsOfUse from "@/views/User/TermsOfUse.vue";
import UserDashboard from "@/views/User/UserDashboard.vue";
import UserSettings from "@/views/User/UserSettings.vue";
import { createRouter, createWebHashHistory } from "vue-router";
import HomePage from "./views/HomePage.vue";

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: HomePage,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/user/dashboard",
    name: "UserDashboard",
    component: UserDashboard,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/login",
    name: "LoginPage",
    component: LoginPage,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/register",
    name: "RegisterPage",
    component: RegisterPage,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/forgot-password",
    name: "ForgotPassword",
    component: ForgotPassword,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/reset-password",
    name: "ResetPassword",
    component: ResetPassword,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/products",
    name: "Products",
    component: ProductsPage,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/single-product/:slug",
    name: "SingleProduct",
    component: SingleProduct,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/products/categories",
    name: "ProductsCategories",
    component: ProductsCategories,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/products/brands",
    name: "ProductsBrand",
    component: ProductsBrand,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/shop-by-category/:slug",
    name: "ShopByCategory",
    component: ShopByCategory,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/shop-by-sub-category/:slug",
    name: "ShopBySubCategory",
    component: ShopBySubCategory,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/shop-by-child-category/:slug",
    name: "ShopByChildCategory",
    component: ShopByChildCategory,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/shop-by-brand/:slug",
    name: "ShopByBrand",
    component: ShopByBrand,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/cart/details",
    name: "CartDetails",
    component: CartDetails,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/wishlist/details",
    name: "WishlistDetails",
    component: WishlistDetails,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/checkout",
    name: "Checkout",
    component: CheckOut,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/edit-profile",
    name: "EditProfile",
    component: EditProfile,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/order-history",
    name: "OrderHistory",
    component: OrderHistory,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/order-details/:id",
    name: "OrderDetails",
    component: OrderDetails,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/notifications",
    name: "NotificationDetails",
    component: NotificationDetails,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/settings",
    name: "UserSettings",
    component: UserSettings,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/privacy-policy",
    name: "PrivacyPolicy",
    component: PrivacyPolicy,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/terms-of-use",
    name: "TermsOfUse",
    component: TermsOfUse,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/order-complete",
    name: "OrderComplete",
    component: OrderComplete,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/contact-us",
    name: "ContactUs",
    component: ContactUs,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: "/contact-complete",
    name: "ContactComplete",
    component: ContactComplete,
    meta: {
      requiresAuth: false,
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const authStatus = useAuthStore().getAuthenticatedStatus();

  if (authStatus && (to.name === "LoginPage" || to.name === "RegisterPage")) {
    next("/user/dashboard");
  } else if (!authStatus && to.meta.requiresAuth) {
    next("/login");
  } else {
    next();
  }
});

export default router;
