<template>
  <div id="app">
    <HeaderPage />
    <router-view />
    <FooterPage />
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import FooterPage from "@/views/FooterPage.vue";
import HeaderPage from "@/views/HeaderPage.vue";
import { onMounted, ref, watch } from "vue";

let cssVariables = ref({});

const fetchCssVariables = async () => {
  try {
    const response = await request.get("/app-color-settings");
    cssVariables.value = response.data.data;
  } catch (error) {
    console.error("Error fetching CSS variables:", error);
  }
};

const applyCssVariables = () => {
  const root = document.documentElement;
  for (const [key, value] of Object.entries(cssVariables.value)) {
    root.style.setProperty(`--${key}`, value);
  }
};

watch(cssVariables, () => {
  applyCssVariables();
});

onMounted(async () => {
  await fetchCssVariables();
});
</script>

<style scoped></style>
