<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/" class="btn btn-light icon-circle">
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center fw-bold">Profile</h3>
      </div>
    </div>

    <div class="">
      <div class="mt-4 d-flex align-items-center">
        <img
          :src="logoPreview"
          alt="Profile Image"
          class="rounded-circle me-3"
          width="100"
          height="100"
        />

        <div>
          <h5 class="mb-1">{{ store.getUserDetails().name }}</h5>
          <p class="text-muted mb-2">{{ store.getUserDetails().email }}</p>
          <router-link :to="{ name: 'EditProfile' }" class="btn btn-primary"
            >Edit Profile</router-link
          >
        </div>
      </div>

      <div class="list-group list-group-flush text-start mt-4">
        <!--<a href="#" class="list-group-item list-group-item-action d-flex align-items-center">
          <i class="bi bi-wallet2 me-3"></i> Payment Methods
        </a>-->
        <router-link
          :to="{ name: 'OrderHistory' }"
          class="list-group-item list-group-item-action d-flex align-items-center"
        >
          <i class="bi bi-bag me-3"></i> Order History
        </router-link>
        <router-link
          :to="{ name: 'WishlistDetails' }"
          class="list-group-item list-group-item-action d-flex align-items-center"
        >
          <i class="bi bi-heart me-3"></i> Wishlist Details
        </router-link>
        <router-link
          :to="{ name: 'NotificationDetails' }"
          class="list-group-item list-group-item-action d-flex align-items-center"
        >
          <i class="bi bi-bell me-3"></i> Notifications
        </router-link>
        <!--<a href="#" class="list-group-item list-group-item-action d-flex align-items-center">
          <i class="bi bi-headset me-3"></i> Support Center
        </a>
        <a href="#" class="list-group-item list-group-item-action d-flex align-items-center">
          <i class="bi bi-shield-check me-3"></i> Legal Policy
        </a>-->
      </div>

      <button @click.prevent="logout" class="btn btn-outline-primary mt-4">
        Log Out
      </button>
    </div>
  </div>
</template>

<script setup>
import { removeToken } from "@/helpers/auth";
import { request } from "@/helpers/request";
import { useAuthStore } from "@/stores/auth";
import { useStore } from "@/stores/mutations";
import toastr from "toastr";
import { ref } from "vue";
import { useRouter } from "vue-router";
const router = useRouter();
const store = useStore();
const authStore = useAuthStore();

const logoPreview = ref(
  store.getUserDetails()?.profile_photo ?? "https://via.placeholder.com/100"
);

const logout = async () => {
  await request
    .post("logout")
    .then((response) => {
      removeToken();
      store.removeToken();
      store.removeUser();
      store.clearCartDetails();
      authStore.logout();
      authStore.removeAuthenticatedStatus();
      toastr.success("Logging out success", "Success");
      router.push("/");
    })
    .catch((error) => {
      console.log(error);
    });
};
</script>

<style scoped>
.container {
  max-width: 500px;
  margin: auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

h1 {
  font-weight: bold;
}

.list-group-item {
  font-weight: 500;
}

.list-group-item i {
  font-size: 1.25rem;
}

.btn-outline-primary {
  width: 100%;
  font-weight: 600;
}
.list-group-item {
  margin-top: 15px;
  border-radius: 10px;
  border: none;
}
</style>
