<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/" class="btn btn-light icon-circle">
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center fw-bold">Contact Us</h3>
      </div>
    </div>

    <div class="form">
      <form @submit.prevent="handleSubmit">
        <div class="mb-3">
          <label for="subject" class="form-label">Subject</label>
          <input
            type="text"
            id="subject"
            v-model="form.subject"
            class="form-control"
            :class="{ 'is-invalid': errors.subject }"
          />
          <div class="invalid-feedback" v-if="errors.subject">
            {{ errors.subject }}
          </div>
        </div>

        <div class="mb-3">
          <label for="name" class="form-label">Name</label>
          <input
            type="text"
            id="name"
            v-model="form.name"
            class="form-control"
            :class="{ 'is-invalid': errors.name }"
          />
          <div class="invalid-feedback" v-if="errors.name">
            {{ errors.name }}
          </div>
        </div>

        <div class="mb-3">
          <label for="email" class="form-label">Email</label>
          <input
            type="email"
            id="email"
            v-model="form.email"
            class="form-control"
            :class="{ 'is-invalid': errors.email }"
          />
          <div class="invalid-feedback" v-if="errors.email">
            {{ errors.email }}
          </div>
        </div>

        <div class="mb-3">
          <label for="phone_number" class="form-label">Phone Number</label>
          <input
            type="text"
            id="phone_number"
            v-model="form.phone_number"
            class="form-control"
            :class="{ 'is-invalid': errors.phone_number }"
          />
          <div class="invalid-feedback" v-if="errors.phone_number">
            {{ errors.phone_number }}
          </div>
        </div>

        <div class="mb-3">
          <label for="comment" class="form-label">Comment</label>
          <textarea
            id="comment"
            v-model="form.comment"
            class="form-control"
            :class="{ 'is-invalid': errors.comment }"
            rows="4"
          ></textarea>
          <div class="invalid-feedback" v-if="errors.comment">
            {{ errors.comment }}
          </div>
        </div>

        <button type="submit" class="btn btn-primary common-btn w-100">
          Send
        </button>
      </form>
    </div>
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import toastr from "toastr";
import { reactive, ref } from "vue";
import { useRouter } from "vue-router";
const router = useRouter();

const form = reactive({
  subject: "",
  name: "",
  phone_number: "",
  email: "",
  comment: "",
});

const errors = ref({});

const validateForm = () => {
  errors.value = {};
  if (!form.subject) errors.value.subject = "Subject is required.";
  if (!form.name) errors.value.name = "Name is required.";
  if (!form.phone_number)
    errors.value.phone_number = "Phone number is required.";
  if (!form.email) {
    errors.value.email = "Email is required.";
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
    errors.value.email = "Enter a valid email.";
  }
  if (!form.comment) errors.value.comment = "Comment is required.";
  return Object.keys(errors.value).length === 0;
};

const handleSubmit = () => {
  if (validateForm()) {
    request.post("contact-us", form).then((response) => {
      toastr.success("Contact us saved successfully", "Success");
      router.push({ name: "ContactComplete" });
    });
  } else {
    toastr.error("Please fill in the required fields.", "Error");
  }
};
</script>

<style scoped>
.container {
  max-width: 500px;
  margin: auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

h3 {
  font-weight: bold;
}
</style>
