export function getToken() {
    return window.localStorage.getItem('ecommerceAuthToken');
}

export function setToken(token) {
    return window.localStorage.setItem('ecommerceAuthToken', token);
}

export function removeToken() {
    return window.localStorage.removeItem('ecommerceAuthToken');
}

export function _hasToken() {
    const _token = getToken();
    return _token !== undefined && _token !== null;
}