<template>
  <div class="container">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link to="/" class="btn btn-light">
          <i class="bi bi-arrow-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center fw-bold">Categories</h3>
      </div>
    </div>

    <div class="row g-3">
      <div class="col-12" v-for="category in categories" :key="category.id">
        <div class="category-card">
          <img :src="category.image_url" :alt="category.category_name" />
          <div class="category-info">
            <router-link
              :to="{ name: 'ShopByCategory', params: { slug: category.slug } }"
              class="nav-link"
            >
              <h5 class="category-title">{{ category.category_name }}</h5>
              <p class="product-count">
                {{ category.category_wise_product_count }} products
              </p>
            </router-link>

            <!-- Subcategory Display -->
            <div v-if="category?.sub_category?.length > 0">
              <h6 class="fw-bold">SUB CATEGORY</h6>
              <ul class="sub-category-list">
                <li
                  v-for="subCategory in category.sub_category"
                  :key="subCategory.id"
                  class="sub-category-item"
                >
                  <router-link
                    :to="{
                      name: 'ShopBySubCategory',
                      params: { slug: subCategory.name },
                    }"
                    class="nav-link"
                  >
                    {{ subCategory.name }}
                  </router-link>
                </li>
              </ul>
            </div>

            <!-- Child Category Display -->
            <div v-if="category?.child_category?.length > 0">
              <h6 class="fw-bold">CHILD CATEGORY</h6>
              <ul class="child-category-list">
                <li
                  v-for="childCategory in category.child_category"
                  :key="childCategory.id"
                  class="child-category-item"
                >
                  <router-link
                    :to="{
                      name: 'ShopByChildCategory',
                      params: { slug: childCategory.name },
                    }"
                    class="nav-link"
                  >
                    {{ childCategory.name }}
                  </router-link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import { onMounted, ref } from "vue";
const categories = ref([]);
const getCategories = () => {
  request
    .get("categories")
    .then((response) => {
      categories.value = response.data.data;
    })
    .catch((error) => {
      console.log(error);
    });
};

onMounted(() => {
  getCategories();
});
</script>

<style scoped>
.category-card {
  border-radius: 8px;
  background-color: #ffffff;
}

.sub-category-list,
.child-category-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.sub-category-item,
.child-category-item {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: #f8f9fa;
  margin-bottom: 5px;
  transition: background-color 0.3s ease;
}

.sub-category-item:hover,
.child-category-item:hover {
  background-color: #e9ecef;
}

.sub-category-item i,
.child-category-item i {
  color: #6c757d;
  font-size: 1rem;
}

.category-title {
  font-size: 1.25rem;
  font-weight: bold;
}

.product-count {
  font-size: 0.9rem;
  color: #6c757d;
}

.child-category-item {
  padding-left: 35px;
  background-color: #f1f3f5;
}
</style>
