import { defineStore } from 'pinia'

export const useAuthStore = defineStore({
    id: 'ecommerceAuthStore',
    state: () => ({
        isAuthenticated: false,
    }),
    actions: {
        login() {
            this.isAuthenticated = true
        },
        logout() {
            this.isAuthenticated = false
        },
        getAuthenticatedStatus() {
            return window.localStorage.getItem('isAuthenticated');
        },
        setAuthenticatedStatus(status) {
            return window.localStorage.setItem('isAuthenticated', status);
        },
        removeAuthenticatedStatus() {
            return window.localStorage.removeItem('isAuthenticated');
        }
    },
    persist: {
        key: 'ecommerceAuthStore',
        serialize: (state) => JSON.stringify(state),
        deserialize: (serializedState) => JSON.parse(serializedState),
    },
})