<template>
  <div class="p-4 pb-0">
    <router-link to="/">
      <span><i class="bi bi-arrow-left-circle fs-1"></i></span>
    </router-link>
  </div>

  <div class="container mt-2 d-flex justify-content-center align-items-center">
    <div class="p-4" style="width: 100%; max-width: 400px;">
      <h2 class="text-center fw-bold">Forgot Password</h2>
      <p class="text-center mb-4">Enter your email address below to receive a verification code</p>
      <form @submit.prevent="submitForgotPassword">
        <div class="mb-3">
          <label for="email" class="form-label">Email</label>
          <input type="email" class="form-control" id="email" v-model="form.email" placeholder="Enter Your Email" autofocus autocomplete="off" />
          <span v-if="errors.email" class="text-danger">{{ errors.email[0] }}</span>
        </div>
        <div class="d-flex justify-content-between align-items-center">
          <button type="submit" class="btn btn-primary w-100 auth-btn">Send Reset Link</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import {reactive, ref} from 'vue';
import {request} from "@/helpers/request";
import {useRouter} from "vue-router";
import {useStore} from "@/stores/mutations";
import toastr from 'toastr';
const router = useRouter()
const store = useStore()

const form = reactive({
  email: ''
})

const errors = ref({
  email: ''
})

const submitForgotPassword = async () => {
    await request.post('forgot-password', {
      email: form.email
    })
    .then(response => {
      console.log(response.data)
        toastr.success('Reset link sent successfully', 'Success');
        router.push('/reset-password')
    })
    .catch(error => {
        if (error.response.data.errors) {
            errors.value = error.response.data.errors
        }
    });
}
</script>

