import { defineStore } from 'pinia'
import { useRouter } from 'vue-router'
const router = useRouter();

export const useStore = defineStore({
    id: 'ecommerceStore',
    state: () => ({
        token: '',
        user: null,
    }),
    actions: {
        getUserDetails() {
            return JSON.parse(window.localStorage.getItem('userDetails'));
        },
        setUser(user) {
            this.user = user
            if (user) {
                window.localStorage.setItem('userDetails', JSON.stringify(user));
            }
        },
        setUserDetails(user) {
            if (user) {
                const userDetails = {
                    "name": user.name,
                }

                window.localStorage.setItem('userDetails', JSON.stringify(userDetails));
            }
        },
        removeUser() {
            this.user = {}
            window.localStorage.removeItem('userDetails');
        },
        setToken(token) {
            this.token = token
        },
        removeToken() {
            this.token = ''
        },
        getCartDetails() {
            return JSON.parse(window.localStorage.getItem('cartDetails'));
        },
        setCartDetails(newCartDetails) {
            if (newCartDetails) {
                let cartDetails = JSON.parse(window.localStorage.getItem('cartDetails')) || {
                    cartItems: {},
                    total_item: 0
                };

                for (let rowId in newCartDetails.cartItems) {
                    if (cartDetails.cartItems[rowId]) {
                        cartDetails.cartItems[rowId].qty += newCartDetails.cartItems[rowId].qty;
                    } else {
                        cartDetails.cartItems[rowId] = newCartDetails.cartItems[rowId];
                    }
                }

                cartDetails.total_item = Object.keys(cartDetails.cartItems).length;
                window.localStorage.setItem('cartDetails', JSON.stringify(cartDetails));
            }
        },
        removeCartDetails(rowId) {
            const cartDetails = JSON.parse(window.localStorage.getItem('cartDetails')) || {};

            if (cartDetails.cartItems && cartDetails.cartItems[rowId]) {
                delete cartDetails.cartItems[rowId];
                cartDetails.total_item = Object.keys(cartDetails.cartItems).length;
                window.localStorage.setItem('cartDetails', JSON.stringify(cartDetails));
            }
        },
        updateQuantity(rowId, newQuantity) {
            const cartDetails = JSON.parse(window.localStorage.getItem('cartDetails')) || { cartItems: {} };

            if (cartDetails.cartItems[rowId]) {
                cartDetails.cartItems[rowId].qty = newQuantity;
                window.localStorage.setItem('cartDetails', JSON.stringify(cartDetails));
            }
        },
        clearCartDetails() {
            window.localStorage.removeItem('cartDetails');
        }
    },
    persist: {
        key: 'ecommerceStore',
        serialize: (state) => JSON.stringify(state),
        deserialize: (serializedState) => JSON.parse(serializedState),
    },
})