<template>
  <div class="container py-4">
    <div class="align-items-center mb-4">
      <div class="d-flex align-items-center">
        <router-link
          :to="{ name: 'UserDashboard' }"
          class="btn btn-light icon-circle"
        >
          <i class="bi bi-chevron-left"></i>
        </router-link>
        <h3 class="mb-0 flex-grow-1 text-center fw-bold">Profile</h3>
      </div>
    </div>

    <div class="">
      <div class="mt-4 text-center">
        <img
          :src="logoPreview"
          alt="Profile Image"
          class="rounded-circle mb-3"
          width="100"
          height="100"
        />
        <!--<input type="file" @change="handleLogoUpload" class="form-control mt-2" accept="image/*">-->
        <input
          type="file"
          ref="fileInput"
          @change="handleLogoUpload"
          class="d-none"
          accept="image/*"
        />
        <button
          @click="triggerFileInput"
          class="btn btn-outline-secondary p-1 mt-2"
          title="Upload Logo"
        >
          <i class="bi bi-camera"></i>
          <!-- Bootstrap icon for image upload -->
        </button>
        <h5 class="mb-1">{{ store.getUserDetails().name }}</h5>
      </div>

      <div class="row mt-4">
        <div class="col-md-6">
          <div class="form-group">
            <label for="name">Name</label>
            <input
              type="text"
              v-model="formData.name"
              class="form-control"
              id="name"
              placeholder="e.g. Mr. X"
            />
          </div>
        </div>

        <div class="col-md-6 mt-2">
          <div class="form-group">
            <label for="email">Email</label>
            <input
              type="email"
              v-model="formData.email"
              class="form-control"
              id="email"
              placeholder="e.g. example@example.com"
            />
          </div>
        </div>

        <div class="col-md-6 mt-2">
          <div class="form-group">
            <label for="phone">Phone</label>
            <input
              type="text"
              v-model="formData.phone"
              class="form-control"
              id="phone"
              placeholder="e.g. 01xxxxxxxxx"
            />
          </div>
        </div>

        <div class="col-md-6 mt-2">
          <div class="form-group">
            <label for="gender">Gender</label>
            <select v-model="formData.gender" class="form-control" id="gender">
              <option value="" selected>Select Gender</option>
              <option value="1">Male</option>
              <option value="2">Female</option>
            </select>
          </div>
        </div>

        <div class="col-md-6 mt-2">
          <div class="form-group">
            <label for="address">Address</label>
            <textarea
              v-model="formData.address"
              id="address"
              cols="30"
              rows="2"
              class="form-control"
              placeholder="House#1, Road#1, Dhaka"
            ></textarea>
          </div>
        </div>

        <div class="col-md-6 mt-2">
          <div class="form-group">
            <label for="password">Password</label>
            <input
              type="password"
              v-model="formData.password"
              class="form-control"
              id="password"
              autocomplete="off"
              placeholder="********"
            />
          </div>
        </div>
      </div>

      <div class="text-center mt-4">
        <router-link
          :to="{ name: 'UserDashboard' }"
          class="btn btn-outline-primary"
          >Done</router-link
        >
        <button @click.prevent="submitForm" class="btn btn-success common-btn">
          Update
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import { useStore } from "@/stores/mutations";
import toastr from "toastr";
import { reactive, ref } from "vue";
import { useRouter } from "vue-router";
const router = useRouter();
const store = useStore();

const formData = reactive({
  name: store.getUserDetails().name ?? "",
  email: store.getUserDetails().email ?? "",
  phone: store.getUserDetails().phone ?? "",
  gender: store.getUserDetails().gender ?? "",
  address: store.getUserDetails().address ?? "",
  password: "",
  image: null,
});

const logoPreview = ref(
  store.getUserDetails()?.profile_photo ?? "https://via.placeholder.com/100"
);
const fileInput = ref(null);

const triggerFileInput = () => {
  fileInput.value.click();
};

const handleLogoUpload = (event) => {
  const file = event.target.files[0];
  if (file) {
    formData.image = file;

    const reader = new FileReader();
    reader.onload = (e) => {
      logoPreview.value = e.target.result;
    };
    reader.readAsDataURL(file);
  }
};

const submitForm = async () => {
  const payload = new FormData();
  Object.keys(formData).forEach((key) => {
    payload.append(key, formData[key]);
  });

  try {
    const response = await request.post("/dashboard/profile/update", payload, {
      headers: { "Content-Type": "multipart/form-data" },
    });
    store.setUser(response.data.data);
    toastr.success("Profile updated", "Success");
    router.push({ name: "UserDashboard" });
  } catch (error) {
    console.error("Error submitting form:", error);
  }
};
</script>

<style scoped>
.container {
  max-width: 500px;
  margin: auto;
  background-color: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
}

h1 {
  font-weight: bold;
}

.common-btn {
  margin-left: 10px;
}
</style>
