<template>
  <div class="d-flex justify-content-between align-items-center p-3 bg-light">
    <!-- Profile Section -->
    <div class="d-flex align-items-center">
      <!--<img :src="profilePhoto" alt="Profile Picture" class="rounded-circle me-2" width="50" height="50"/>-->
      <img :src="appLogo" alt="App Logo" class="me-2" width="50" height="50" />

      <div v-if="store.getUserDetails()?.name?.length > 0">
        <h5 class="mb-0">Welcome,</h5>
        <h6 class="fw-bold">{{ store.getUserDetails()?.name }}</h6>
      </div>
    </div>

    <!-- Notification Icon -->
    <div class="d-flex align-items-center">
      <router-link :to="{ name: 'NotificationDetails' }">
        <i class="bi bi-bell position-relative" style="font-size: 17px">
          <span
            class="position-absolute top-0 start-100 translate-middle p-1 bg-success border border-light rounded-circle"
            style="width: 8px; height: 8px"
          ></span>
        </i>
      </router-link>
    </div>
  </div>
</template>

<script setup>
import { request } from "@/helpers/request";
import { useStore } from "@/stores/mutations";
import { onMounted, ref } from "vue";
const store = useStore();
const appLogo = ref(null);

const defaultLogo =
  "https://fakeimg.pl/50x50/2bb847/ffffff?text=e-com&font=museo";

// Function to update the favicon dynamically
const updateFavicon = (iconUrl) => {
  let link = document.querySelector("link[rel~='icon']");

  if (!link) {
    link = document.createElement("link");
    link.rel = "icon";
    document.head.appendChild(link);
  }
  link.href = iconUrl;
};

const getAllLogo = () => {
  request
    .get("app-logo")
    .then((response) => {
      appLogo.value = response?.data?.data ?? defaultLogo;
      updateFavicon(appLogo.value);
    })
    .catch((error) => {
      console.log(error);
      appLogo.value = defaultLogo;
      updateFavicon(defaultLogo);
    });
};

onMounted(() => {
  getAllLogo();
});
</script>
