<template>
  <nav class="navbar fixed-bottom bg-light shadow-lg">
    <div
      class="container d-flex justify-content-between align-items-center position-relative p-0 m-0"
    >
      <!-- Icons -->
      <div class="d-flex justify-content-around align-items-center w-100">
        <div :class="{ 'highlight-icon': activeTab === 'cart' }">
          <router-link to="/cart/details" @click="setActive('cart')">
            <div :class="{ 'icon-circle': activeTab === 'cart' }">
              <i
                class="bi bi-cart2 nav-icon"
                :class="{ 'icon-white': activeTab === 'cart' }"
              ></i>
            </div>
          </router-link>
        </div>
        <div :class="{ 'highlight-icon': activeTab === 'wishlist' }">
          <router-link
            :to="{ name: 'WishlistDetails' }"
            @click="setActive('wishlist')"
          >
            <div :class="{ 'icon-circle': activeTab === 'wishlist' }">
              <i
                class="bi bi-heart nav-icon"
                :class="{ 'icon-white': activeTab === 'wishlist' }"
              ></i>
            </div>
          </router-link>
        </div>

        <!-- Central Icon (Highlighted) -->
        <div :class="{ 'highlight-icon': activeTab === 'home' }">
          <router-link to="/" @click="setActive('home')">
            <div :class="{ 'icon-circle': activeTab === 'home' }">
              <i
                class="bi bi-house-door nav-icon"
                :class="{ 'icon-white': activeTab === 'home' }"
              ></i>
            </div>
          </router-link>
        </div>

        <div :class="{ 'highlight-icon': activeTab === 'settings' }">
          <router-link
            :to="{ name: 'UserSettings' }"
            @click="setActive('settings')"
          >
            <div :class="{ 'icon-circle': activeTab === 'settings' }">
              <i
                class="bi bi-gear nav-icon"
                :class="{ 'icon-white': activeTab === 'settings' }"
              ></i>
            </div>
          </router-link>
        </div>
        <div :class="{ 'highlight-icon': activeTab === 'profile' }">
          <router-link to="/user/dashboard" @click="setActive('profile')">
            <div :class="{ 'icon-circle': activeTab === 'profile' }">
              <i
                class="bi bi-person nav-icon"
                :class="{ 'icon-white': activeTab === 'profile' }"
              ></i>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </nav>
</template>

<script setup>
import { onMounted, ref, watch } from "vue";
import { useRoute } from "vue-router";

const route = useRoute();
const activeTab = ref("home");

const setActive = (tab) => {
  activeTab.value = tab;
};

// Function to update activeTab based on route
const updateActiveTab = () => {
  const path = route.path;

  if (path.includes("cart")) activeTab.value = "cart";
  else if (path.includes("wishlist")) activeTab.value = "wishlist";
  else if (path.includes("settings")) activeTab.value = "settings";
  else if (path.includes("dashboard")) activeTab.value = "profile";
  else activeTab.value = "home";
};

// Run on initial mount
onMounted(updateActiveTab);

// Watch for route changes and update active tab
watch(route, updateActiveTab);
</script>

<style scoped>
.navbar {
  height: 8%;
  padding: 0;
}

.nav-icon {
  font-size: 1.8rem;
  color: #6c757d;
}

.highlight-icon {
  transition: transform 0.1s ease;
  transform: translateY(-25%);
}

.icon-circle {
  background-color: #20c997;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 8px rgb(0 0 0 / 46%);
}

.icon-white {
  color: white;
}

.side-icon {
  font-size: 1.8rem;
}

.bi-cart2,
.bi-heart,
.bi-gear,
.bi-person {
  cursor: pointer;
}

.shadow-lg {
  box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.15);
}
</style>
