<template>
  <div class="modal fade show" tabindex="-1" style="display: block;" aria-modal="true" role="dialog">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Product Filter</h5>
          <button type="button" class="btn-close" @click="$emit('close')"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-6">
                <!-- Category Selection -->
                <h5>Filter By Category</h5>
                <div class="d-flex flex-wrap">
                  <label class="custom-radio-checkbox mb-2"
                         v-for="(category, index) in categories"
                         :key="index"
                         :class="{ 'active': selectedCategory === category.id }"
                  >
                    <input
                        type="radio"
                        name="category"
                        :value="category.id"
                        v-model="selectedCategory"
                        class="d-none"
                    />
                    <span class="checkbox-lookalike">{{ category.category_name }}</span>
                  </label>
                </div>

                <!-- Brand Selection -->
                <h5 class="mt-3">Filter By Brand</h5>
                <div class="d-flex flex-wrap">
                  <label class="custom-radio-checkbox mb-2"
                         v-for="(brand, index) in brands"
                         :key="index"
                         :class="{ 'active': selectedBrand === brand.id }"
                  >
                    <input
                        type="radio"
                        name="brand"
                        :value="brand.id"
                        v-model="selectedBrand"
                        class="d-none"
                    />
                    <span class="checkbox-lookalike">{{ brand.brand_name }}</span>
                  </label>
                </div>

              <!-- Color Selection -->
              <h5 class="mt-3">Filter By Color</h5>
              <div class="d-flex flex-wrap">
                <label class="custom-radio-checkbox mb-2"
                       v-for="(color, index) in colors"
                       :key="index"
                       :class="{ 'active': selectedColor === color.id }"
                >
                  <input
                      type="radio"
                      name="color"
                      :value="color.id"
                      v-model="selectedColor"
                      class="d-none"
                  />
                  <span class="checkbox-lookalike">{{ color.name }}</span>
                </label>
              </div>

              <!-- Size Selection -->
              <h5 class="mt-3">Filter By Size</h5>
              <div class="d-flex flex-wrap">
                <label class="custom-radio-checkbox mb-0"
                       v-for="(size, index) in sizes"
                       :key="index"
                       :class="{ 'active': selectedSize === size.id }"
                >
                  <input
                      type="radio"
                      name="size"
                      :value="size.id"
                      v-model="selectedSize"
                      class="d-none"
                  />
                  <span class="checkbox-lookalike">{{ size.name }}</span>
                </label>
              </div>

              <div>
                <button @click.prevent="applyFilter" class="btn btn-success mt-3 cart-btn">Apply Filter</button>
                <button @click.prevent="resetFilter" class="btn btn-outline-danger mt-3 ms-2">Reset</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';
import {request} from "@/helpers/request";
import {useStore} from "@/stores/mutations";
import router from "@/router";

const selectedCategory = ref(null);
const selectedBrand = ref(null);
const selectedColor = ref(null);
const selectedSize = ref(null);

const props = defineProps({
  categories: {
    type: Object,
    required: true,
  },
  brands: {
    type: Object,
    required: true,
  },
  colors: {
    type: Object,
    required: true,
  },
  sizes: {
    type: Object,
    required: true,
  },
});

const emit = defineEmits(['apply', 'close']);

const resetFilter = () => {
  selectedCategory.value = null;
  selectedBrand.value = null;
  selectedColor.value = null;
  selectedSize.value = null;
}

const applyFilter = () => {
  emit('apply', {
    category: selectedCategory.value,
    brand: selectedBrand.value,
    color: selectedColor.value,
    size: selectedSize.value,
  });
};
</script>

<style scoped>
.modal {
  background-color: rgba(0, 0, 0, 0.5);
}

.custom-radio-checkbox {
  cursor: pointer;
  margin-right: 10px;
}

.custom-radio-checkbox .checkbox-lookalike {
  display: inline-block;
  padding: 8px 12px;
  border: 2px solid #ccc;
  border-radius: 5px;
  color: #333;
  transition: background-color 0.3s, color 0.3s;
}

.custom-radio-checkbox.active .checkbox-lookalike {
  background-color: #00ABB3;
  color: #fff;
  border-color: #00ABB3;
}
</style>